@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&display=swap);
header div{text-align:right;margin-right:20px;margin-top:20px}header h1{text-align:center;color:#00061a;line-height:1.5em;padding:40px;font-weight:600;font-size:40px}

.article{display:grid;grid-gap:20px;grid-template-columns:1fr;-webkit-align-items:stretch;align-items:stretch;padding:20px;box-shadow:0px 0px 2px #f5f5f5;border-radius:7px;background-color:#f8f8f8}.article img{width:100%;border-radius:7px;object-fit:cover}.article div{text-align:center}.article div h3{font-size:25px;font-weight:600;letter-spacing:0.5px;color:#00061a;margin-bottom:20px}.article div p{color:#363850;letter-spacing:0.5px;font-size:16px;line-height:1.4em;margin-bottom:20px}.article div span{display:block}.article div span a{text-decoration:none;color:#00061a;transition:0.4s;position:relative;padding-bottom:2px;font-weight:500}.article div span a:after{content:"";position:absolute;bottom:0px;left:0px;height:2px;background-color:#00061a}.article div span a:hover:after{width:100%;-webkit-animation:animate .5s ease-out;animation:animate .5s ease-out}@-webkit-keyframes animate{0%{width:0%}100%{width:100%}}@keyframes animate{0%{width:0%}100%{width:100%}}@media all and (max-width: 1000px){.double{grid-template-columns:1fr}}

*{font-family:"Montserrat", arial;margin:0px;padding:0px}.container--article{display:grid;grid-template-columns:1fr 1fr;grid-gap:20px;max-width:1200px;margin:auto}.btn-primary{width:200px;height:50px;margin-bottom:50px;margin-top:50px;margin-left:auto;margin-right:auto;display:block;border:none;border-radius:7px;background-color:#00061a;color:#f0f0f0;font-size:18px;letter-spacing:0.5px;font-weight:600;text-transform:uppercase;cursor:pointer;outline:none;box-shadow:0px 2px 10px -3px #111111;transition:.4s}.btn-primary:hover{position:relative;top:2px}@media all and (max-width: 800px){.container--article{grid-template-columns:1fr}.container--article div{max-width:80%;margin:auto}}

